import React from 'react';
import Card from "react-bootstrap/Card";
import Image from "react-bootstrap/Image";
import { Link } from "react-router-dom";
import Button from "react-bootstrap/Button";
import qrcode from "../assets/qrcode.jpg";
import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";




// export default class CartTotalsCart(props) {
// class CartTotalsCart extends Component {



function drawNoContent(){
    
}

export default function CartTotalsCart(props) {
    const [status, setStatus] = React.useState(0) // 0: no show, 1: show yes, 2: show no.
    // const [radioValue, setRadioValue] = React.useState('1');


  const radioHandler = (status) => {
    setStatus(status);
  };

  
const pickup = <>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        手机号： 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        取餐时间: 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
            </>
const deliver = <>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        手机号： 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        送餐时间: 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        住宅地址: 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
                </>

const deliverFee = <>
                    <Card.Body className='d-flex justify-content-between p-2'>
                        <Card.Text>
                            运费(满$80，小于5公里内免运费)
                        </Card.Text>
                        <Card.Text>
                            ${5.00}
                        </Card.Text>
                    </Card.Body>
                </>
    const radios = [
        { name: 'Pick up', value: '1' },
        { name: 'Delivery', value: '2' }
      
    ];
        return (
            <div>
                  <ToggleButtonGroup type="checkbox">
                      请选择：
                    <ToggleButton checked={status === 1} id="tbg-btn-1" value={1} onClick={(e) => radioHandler(1)} variant="success">
                        自取 Pickup
                    </ToggleButton>{' '}
                    {' '}
                    <ToggleButton checked={status === 2} id="tbg-btn-2" value={2} onClick={(e) => radioHandler(2)} variant="secondary">
                        送餐 Delivery
                    </ToggleButton>
       
                </ToggleButtonGroup>

            
                    {/* <input type="radio" name="release" checked={status === 1} onClick={(e) => radioHandler(1)} />
                    <input type="radio" name="release" checked={status === 2} onClick={(e) => radioHandler(2)} /> */}
                    {/* {status === 1 && drawYesContent()} */}
                    {/* {status === 2 && drawNoContent()} */}
                
                 { status === 1 ? (pickup) : (deliver)  }
                {/* <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        手机号： 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body>
                <Card.Body className='d-flex justify-content-between p-2'>
                    <Card.Text>
                        取餐时间: 
                    </Card.Text>
                    <Card.Text>
                        <input />
                    </Card.Text>
                </Card.Body> */}
                    {/* <Card.Body className='d-flex justify-content-between p-2'>
                        <Card.Text>
                            住宅地址: 
                        </Card.Text>
                        <Card.Text>
                            <input />
                        </Card.Text>
                    </Card.Body> */}

                <h4 className='text-uppercase my-auto'>Cart Totals</h4>
                <Card className='mt-3'>
                    

                    <Card.Header className='pl-2 font-weight-bold'>
                        {props.total} Products
                    </Card.Header>
                    <Card.Body className='d-flex justify-content-between p-2 mt-3'>
                        <Card.Text>
                            Products Price
                        </Card.Text>
                        <Card.Text>
                            ${props.totalPrice}
                        </Card.Text>
                    </Card.Body>

                    { status === 2 ? (deliverFee) : null }

                    <Card.Body className='d-flex justify-content-between p-2'>
                        <Card.Text>
                            总计 Total
                        </Card.Text>
                        <Card.Text>
                            {/* ${props.totalPrice + 5.00} */}
                            ${ status === 2 ? props.totalPrice + 5.00 : props.totalPrice  }
                            
                        </Card.Text>
                    </Card.Body>

                    
                    

                    <Card.Footer>
                        {/* <Link to='checkout'>
                            <Button variant='warning' block disabled={props.disabled}>
                                {props.buttonText}
                            </Button>
                        </Link> */}
                        <Button variant='warning' block disabled={props.disabled}>
                            {/* {props.buttonText} */}
                            请截图后发送到群里
                            <img height="150px" width="150px" src={qrcode}/>
                        </Button>
                        
                        <hr/>

                        <h6 className='mt-3'>
                            接受银行转账
                            {/* <Image className="img-thumbnail mt-1" src="https://assets.asosservices.com/asos-finance/images/marketing/single.png"
                                   alt="payment options"/> */}
                        </h6>
                        <p style={{fontSize: '0.8em'}}>
                            也支持微信付款，请扫码入群，获取付款二维码。
                        </p>
                    </Card.Footer>
                </Card>
            </div>
        );
}