import React from 'react';
import CustomParallax from '../components/CustomParallax'
import qrcode from "../assets/qrcode.jpg";

export default function Contact() {
    return (
        <React.Fragment>
            <CustomParallax title='Deliveries' img={qrcode} height={300}/>
            {/* 扫码入群，截图下单，快捷便利。 */}
        </React.Fragment>
    );
}