import React, { Component } from 'react';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import DeliveryCard from "./DeliveryCard";
import { connect } from "react-redux";
import {Form, FormControl} from "react-bootstrap";
import Button from "react-bootstrap/Button";
import { FaSearch } from "react-icons/fa";
import InputGroup from "react-bootstrap/InputGroup";
// import Header from './SideBar'

class DeliveryMenu extends Component {
    state = {
        search: ''
    };

    handleChange = (e) => {
        this.setState({search: e.target.value.toUpperCase()})
    };


    itemsIterate = (meal) => {
        return Object.values(this.props.items)
            .filter(item =>
                item.meal === meal && (
                    item.title.toUpperCase().includes(this.state.search) ||
                    item.description.toUpperCase().includes(this.state.search) ||
                    item.description2.toUpperCase().includes(this.state.search) ||
                    item.price.toUpperCase().includes(this.state.search)    ||
                    item.meal.toUpperCase() === this.state.search
                    )
            ).map(item => this.cardRender(item));
    };

    cardRender = (item) => <DeliveryCard img={item.img} title={item.title} price={item.price} description={item.description} num={item.num}/>;

    render() {
        // const appetizer = this.itemsIterate('appetizer'),
        //     main = this.itemsIterate('main'),
        //     desert = this.itemsIterate('desert');

        const   A = this.itemsIterate('燒味類 BBQ Style'),
                B = this.itemsIterate('燒味飯 BBQ Style with steamed Rice'),
                C = this.itemsIterate('燴飯類 Stirred Fry on Steamed Rice'),
                D = this.itemsIterate('炒飯類 Fried Rice'),
                E = this.itemsIterate('湯飯麵條 Noodle,Rice Noodles,Vermicelli with soup'),
                F = this.itemsIterate('炒飯/炒麵類 Fried Rice Noodles/Noodles'),
                G = this.itemsIterate('粥類 Congee'),
                H = this.itemsIterate('小食類 Snack'),
                I = this.itemsIterate('湯類 Soup'),
                
                J =this.itemsIterate('海鮮類 Seafood'),
                K = this.itemsIterate('飲類 Drinks'),
                L = this.itemsIterate('2人套餐 Combo A for 2 person'),
                M = this.itemsIterate('雞/豬/牛類 Chicken/pork/beef'),
                N = this.itemsIterate('煲仔類 Hot Pot'),
                O = this.itemsIterate('鐵板類 Sizzling'),
                P = this.itemsIterate('時菜豆腐類 vege/Tofu'),
                Q = this.itemsIterate('4人套餐 Combo A for 4 person')
                ;

        return (
            
            <Container className='Delivery'>
                {/* <Header/> */}
               
                <div className='desktop-only'>
                    <Button variant='primary' value='' onClick={this.handleChange} className='mt-4' size='sm'>
                    全部 All
                    </Button>{' '}
                    <Button variant='warning' value='燒味類 BBQ Style' onClick={this.handleChange} className='mt-4' size='sm'>
                    燒味類 BBQ STYLE
                    </Button>{' '}
                    <Button variant='warning' value='燒味飯 BBQ Style with steamed Rice' onClick={this.handleChange} className='mt-4' size='sm'>
                    燒味飯 BBQ Style with steamed Rice
                    </Button>{' '}
                    <Button variant='warning' value='燴飯類 Stirred Fry on Steamed Rice' onClick={this.handleChange} className='mt-4' size='sm'>
                    燴飯類 Stirred Fry on Steamed Rice
                    </Button>{' '}
                    <Button variant='warning' value='炒飯類 Fried Rice' onClick={this.handleChange} className='mt-4' size='sm'>
                    炒飯類 Fried Rice
                    </Button>{' '}
                    <Button variant='warning' value='湯飯麵條 Noodle,Rice Noodles,Vermicelli with soup' onClick={this.handleChange} className='mt-4' size='sm'>
                    湯飯麵條 Noodle,Rice Noodles,Vermicelli with soup
                    </Button>{' '}
                    <Button variant='warning' value='炒飯/炒麵類 Fried Rice Noodles/Noodles' onClick={this.handleChange} className='mt-4' size='sm'>
                    炒飯/炒麵類 Fried Rice Noodles/Noodles
                    </Button>{' '}
                    <Button variant='warning' value='粥類 Congee' onClick={this.handleChange} className='mt-4' size='sm'>
                    粥類 Congee
                    </Button>{' '}
                    <Button variant='warning' value='小食類 Snack' onClick={this.handleChange} className='mt-4' size='sm'>
                    小食類 Snack
                    </Button>{' '}
                    <Button variant='warning' value='湯類 Soup' onClick={this.handleChange} className='mt-4' size='sm'>
                    湯類 Soup
                    </Button>{' '}
                    <Button variant='warning' value='海鮮類 Seafood' onClick={this.handleChange} className='mt-4' size='sm'>
                    海鮮類 Seafood
                    </Button>{' '}
                    <Button variant='warning' value='飲類 Drinks' onClick={this.handleChange} className='mt-4' size='sm'>
                    飲類 Drinks
                    </Button>{' '}
                    <Button variant='warning' value='雞/豬/牛類 Chicken/pork/beef' onClick={this.handleChange} className='mt-4' size='sm'>
                    雞/豬/牛類 Chicken/pork/beef
                    </Button>{' '}
                    <Button variant='warning' value='煲仔類 Hot Pot' onClick={this.handleChange} className='mt-4' size='sm'>
                    煲仔類 Hot Pot
                    </Button>{' '}
                    <Button variant='warning' value='鐵板類 Sizzling' onClick={this.handleChange} className='mt-4' size='sm'>
                    鐵板類 Sizzling
                    </Button>{' '}
                    <Button variant='warning' value='時菜豆腐類 vege/Tofu' onClick={this.handleChange} className='mt-4' size='sm'>
                    時菜豆腐類 vege/Tofu
                    </Button>{' '}
                    <Button variant='info' value='2人套餐 Combo A for 2 person' onClick={this.handleChange} className='mt-4' size='sm'>
                    2人套餐 Combo A for 2 person
                    </Button>{' '}
                    <Button variant='info' value='4人套餐 Combo A for 4 person' onClick={this.handleChange} className='mt-4' size='sm'>
                    4人套餐 Combo A for 4 person
                    </Button>{' '}
                </div>

                <Form className='justify-content-center' inline >
                    <InputGroup style={{width: "50%",height:"100%"}}>
                        <FormControl onChange={this.handleChange}  type="text" placeholder='在此快速搜索菜品(繁/简/英)'/>
                        <InputGroup.Append>
                            <InputGroup.Text id="inputGroupPrepend">
                                <FaSearch/>
                            </InputGroup.Text>
                        </InputGroup.Append>
                    </InputGroup>
                </Form>

                {A.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>燒味類 BBQ Style</h1>
                    <Row className='pb-3'>
                        {A}
                    </Row>
                </>}

                {B.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>燒味飯 BBQ Style with steamed Rice</h1>
                    <Row className='pb-3'>
                        {B}
                    </Row>
                </>}

                {C.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>燴飯類 Stirred Fry on Steamed Rice</h1>
                    <Row className='pb-3'>
                        {C}
                    </Row>
                </>}

                {D.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>炒飯類 Fried Rice</h1>
                    <Row className='pb-3'>
                        {D}
                    </Row>
                </>}

                {E.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>湯飯麵條(撈麵加$2) Noodle,Rice Noodles,Vermicelli with soup(Dry noodles extra $2)</h1>
                    <Row className='pb-3'>
                        {E}
                    </Row>
                </>}

                {F.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>炒飯/炒麵類 Fried Rice Noodles/Noodles</h1>
                    <Row className='pb-3'>
                        {F}
                    </Row>
                </>}

                {G.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>粥類 Congee</h1>
                    <Row className='pb-3'>
                        {G}
                    </Row>
                </>}

                {H.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>小食類 Snack</h1>
                    <Row className='pb-3'>
                        {H}
                    </Row>
                </>}

                {I.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>湯類 Soup</h1>
                    <Row className='pb-3'>
                        {I}
                    </Row>
                </>}

                {J.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>海鮮類 Seafood</h1>
                    <Row className='pb-3'>
                        {J}
                    </Row>
                </>}

                {K.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>飲類 Drinks</h1>
                    <Row className='pb-3'>
                        {K}
                    </Row>
                </>}

                {L.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>2人套餐 Combo A for 2 person $68</h1>
                    <Row className='pb-3'>
                        {L}
                    </Row>
                </>}
                {Q.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>4人套餐 Combo A for 4 person $110</h1>
                    <Row className='pb-3'>
                        {Q}
                    </Row>
                </>}

                {M.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>雞/豬/牛類 Chicken/pork/beef</h1>
                    <Row className='pb-3'>
                        {M}
                    </Row>
                </>}
                
                {N.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>煲仔類 Hot Pot</h1>
                    <Row className='pb-3'>
                        {N}
                    </Row>
                </>}

                {O.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>鐵板類 Sizzling</h1>
                    <Row className='pb-3'>
                        {O}
                    </Row>
                </>}

                {P.length > 0 &&
                <>
                    <h1 className='pt-5 mx-auto'>時菜豆腐類 vege/Tofu</h1>
                    <Row className='pb-3'>
                        {P}
                    </Row>
                </>}



                {(C.length === 0 && B.length === 0 && A.length === 0
                && D.length === 0 && E.length === 0 && F.length === 0
                && G.length === 0 && H.length === 0 && I.length === 0
                && J.length === 0 && K.length === 0 && L.length === 0
                && M.length === 0 && N.length === 0 && O.length === 0 
                && P.length === 0 && Q.length === 0) &&
                <div className='text-center mt-5'>
                    <h4>
                        Your search didn't match any item
                    </h4>
                </div>
                }

            </Container>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        items: state.cart.items
    }
};

export default connect(mapStateToProps)(DeliveryMenu);